import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-sidenav',
  templateUrl: './sidenav.component.html',
  styleUrls: ['./sidenav.component.css']
})
export class SidenavComponent implements OnInit {

  sideNavListItem: any = ['Home', 'train with us', 'gallery', 'our team', 'find us'];
  test: any = [];
  isOpen: any = false;
  constructor() { }

  ngOnInit() {
    for (let index = 0; index < this.sideNavListItem.length; index++) {
      this.test[index] = false;
    }
  }

  navState(prm) {
    this.test[prm] = !this.test[prm];
    for (let index = 0; index < this.test.length; index++) {
      if (index === prm) {
        this.test[index] = true;
      } else {
        this.test[index] = false;
      }
    }
  }

}
