import { Component, OnInit } from "@angular/core";

@Component({
  selector: "app-train-with-us",
  templateUrl: "./train-with-us.component.html",
  styleUrls: ["./train-with-us.component.css"]
})
export class TrainWithUsComponent implements OnInit {
  twsObj: any = {
  twsList: [
      {
        name: "Running",
        imageUrl: "../../assets/images/banners/h2-banner-img-1.jpg",
        effectingPart:
          "liquam sint delectus repellendus quis quia necessitatibus. Eligendi, earum dignissimos! Fugiat doloremque repellat laudantium aliquam?liquam sint delectus repellendus quis quia necessitatibus. Eligendi, earum dignissimos! Fugiat doloremque repellat laudantium aliquam?",
        benifits:
          "liquam sint delectus repellendus quis quia necessitatibus. Eligendi, earum dignissimos! Fugiat doloremque repellat laudantium aliquamliquam sint delectus repellendus quis quia necessitatibus. Eligendi, earum dignissimos! Fugiat doloremque repellat laudantium aliquam??"
      },
      {
        name: "plank",
        imageUrl: "../../assets/images/banners/h2-banner-img-2.jpg",
        effectingPart:
          "liquam sint delectus repellendus quis quia necessitatibus",
        benifits:
          "liquam sint delectus repellendus quis quia necessitatibus. Eligendi, earum dignissimos! Fugiat doloremque repellat laudantium aliquam?"
      },
      {
        name: "bench press",
        imageUrl: "../../assets/images/banners/h2-banner-img-3.jpg",
        effectingPart:
          "liquam sint delectus repellendus quis quia necessitatibus. Eligendi, earum dignissimos! Fugiat doloremque repellat laudantium aliquam?",
        benifits:
          "liquam sint delectus repellendus quis quia necessitatibus."
      }
    ]
  };
  sliderControlerArg: any = [];
  sliderControlerVar: any = 0;

  constructor() {}

  ngOnInit() {
    this.sliderControlerArg = new Array<number>(this.twsObj.twsList.length);
  }

  slideControler(prm) {
    if (prm === 'increment') {
      this.sliderControlerVar ++;
      if (this.sliderControlerVar > this.sliderControlerArg.length - 1) {
        this.sliderControlerVar = 0;
      }
    } else {
      this.sliderControlerVar --;
      if (0 > this.sliderControlerVar) {
        this.sliderControlerVar = this.sliderControlerArg.length - 1;
      }
    }
  }
}
